/**
 * Settings
 */

// Fonts
$font-main: sans-serif;
$font-alt: sans-serif;
$font-size: 16px;
$font-color: #444;

// Colors
$color-navy: #001f3f;
$color-blue: #0074d9;
$color-aqua: #7fdbff;
$color-teal: #39cccc;
$color-olive: #3d9970;
$color-green: #2ecc40;
$color-lime: #01ff70;
$color-yellow: #ffdc00;
$color-orange: #ff851b;
$color-red: #d13f19;
$color-maroon: #85144b;
$color-fuchsia: #f012be;
$color-purple: #2f192e;
$color-white: #fff;
$color-silver: #ddd;
$color-gray: #aaa;
$color-black: #222;

$color-error: $color-red;
$color-success: $color-green;
$color-background: $color-white;

// Media query breakpoints
$breakpoints: (
    xs: 500px,
    sm: 750px,
    md: 992px,
    lg: 1200px,
    xl: 1440px
);
